\r\n\r\n@import \"pages/root\";\r\n@import \"pages/print\";\r\n@import \"pages/storypage\";\r\n@import \"pages/teampage\";","@keyframes roll {\r\n  0% {\r\n    transform: translateY(0);\r\n  }\r\n  50% {\r\n    transform: rotate(random(360) + deg) translateY(random(4) + rem);\r\n  }\r\n  100% {\r\n    transform: translateY(0);\r\n  }\r\n}\r\n@keyframes rotate {\r\n  from {\r\n    transform: rotate(0);\r\n  }\r\n  50% {\r\n    border-radius: 0px;\r\n  }\r\n  to {\r\n    transform: rotate(360deg);\r\n  }\r\n}\r\n@keyframes hue-rotate {\r\n  0% {\r\n    filter: hue-rotate(0deg);\r\n  }\r\n  100% {\r\n    filter: hue-rotate(360deg);\r\n  }\r\n}\r\n\r\n.shift {\r\n  transition: all 0.5s;\r\n  &-left {\r\n    transform: translate(-50vw, 0);\r\n  }\r\n  &-right {\r\n    transform: translate(50vw, 0);\r\n  }\r\n  &-down {\r\n    transform: translate(0, 50vh);\r\n  }\r\n  &-up {\r\n    transform: translate(0, -50vh);\r\n  }\r\n}\r\n\r\n@keyframes animate {\r\n  0% {\r\n    content: \"[Hello World]\";\r\n  }\r\n\r\n  25% {\r\n    content: \"[Bonjour le monde]\";\r\n  }\r\n\r\n  50% {\r\n    content: \"[Hola Mundo]\";\r\n  }\r\n\r\n  75% {\r\n    content: \"[Selam Dnya]\";\r\n  }\r\n\r\n  100% {\r\n    content: \"[Ol Mundo]\";\r\n  }\r\n}\r\n@keyframes rotateBlob {\r\n  100% {\r\n      transform: rotate(360deg);\r\n  }\r\n}\r\n@keyframes fadeIn {\r\n  from {\r\n      opacity: 0;\r\n  }\r\n  to {\r\n    opacity: 1;\r\n  }\r\n}\r\n.out-top {\r\n  animation: rotate 20s linear infinite;\r\n  transform-origin: 13px 25px;\r\n}\r\n.in-top {\r\n  animation: rotate 10s linear infinite;\r\n  transform-origin: 13px 25px;\r\n}\r\n.out-bottom {\r\n  animation: rotate 25s linear infinite;\r\n  transform-origin: 84px 93px;\r\n}\r\n.in-bottom {\r\n  animation: rotate 15s linear infinite;\r\n  transform-origin: 84px 93px;\r\n}\r\n.fade-in {\r\n  animation: fadeIn .75s linear;\r\n}",".right {\r\n  position: absolute;\r\n  top: -5vh;\r\n  right: 5vw;\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  transition: .5s all;\r\n  @media screen and (max-width:1450px){\r\n    position:static;\r\n  }\r\n}\r\n\r\ndiv[data-completed=false][data-inprogress=true]{\r\n  display: block;\r\n}\r\n.hide, div[data-completed=true], div[data-inprogress=false] {\r\n    display: none;\r\n}","/*\r\nCOLORS:\r\n\r\nGabriela's Super Purple Background: #955AFF\r\nA great bottom purple: #c63cc8\r\n\r\n*/\r\n\r\n// Colors\r\n$color-random: hsl(\r\n    random(360),\r\n    44 + floor(random() * ((56 - 44) + 1)),\r\n    44 + floor(random() * ((56 - 44) + 1))\r\n  );\r\n\r\n$color-primary: #955AFF;\r\n$color-tertiary: #5a9bff;\r\n$color-primary-light: #C9C5C5;\r\n$color-primary-dark:  #0711086c;\r\n\r\n$color-secondary: #ffffff;\r\n$color-secondary-light:#ffb900;\r\n$color-secondary-dark: #ff7730;\r\n\r\n$color-tertiary-light: #2998ff;\r\n$color-tertiary-dark: #5643fa;\r\n\r\n$color-grey-light-1: #f7f7f7;\r\n$color-grey-light-2: #eeeeee;\r\n    \r\n$color-grey-dark-1: #777;\r\n$color-grey-dark-2: #999;\r\n$color-grey-dark-3: #333;\r\n\r\n$color-card-light: #000;\r\n$color-card-dark: #fff;\r\n\r\n$color-green: green;\r\n$color-yellow: hsl(41, 97%, 56%);\r\n$color-white: #fff;\r\n$color-black: #000;\r\n$color-transwhite: hsla(0, 0%, 100%, 0.226);\r\n$color-shadow:  rgba(0, 0, 0, 0.2);\r\n\r\n// Grid\r\n$grid-width: 114rem;\r\n$gutter-vertical: 8rem;\r\n$gutter-vertical-small: 6rem;\r\n$gutter-horizontal: 6rem;\r\n\r\n// Font\r\n$default-font-size: 1.6rem;\r\n\r\n// Border\r\n$border-radius-value: 3px;\r\n$border-radius-leaf-value: 5rem;","*,\r\n*::after,\r\n*::before {\r\n  margin: 0;\r\n  padding: 0;\r\n  box-sizing: inherit;\r\n/*  box-sizing: border-box;*/\r\n}\r\n\r\nhtml {\r\n    // This defines what 1rem is\r\n  font-size: 62.5%; // 1 rem = 10px;\r\n}\r\n\r\nbody {\r\n  box-sizing: border-box;\r\n  padding: 3rem;\r\n  \r\n\r\n}\r\n","footer {\r\n  transition: all 0.5s;\r\n  background: linear-gradient(\r\n    45deg,\r\n    #0054676e,\r\n    hsl(\r\n      random(360),\r\n      44 + floor(random() * ((56 - 44) + 1)),\r\n      44 + floor(random() * ((56 - 44) + 1))\r\n    )\r\n  );\r\n  position: fixed;\r\n  left: 0;\r\n  bottom: 0;\r\n  width: 100%;\r\n  text-align: center;\r\n  a {\r\n    color: $color-secondary;\r\n    text-decoration: none;\r\n    &:hover {\r\n      color: $color-secondary-light;\r\n      text-decoration: underline;\r\n    }\r\n  }\r\n}\r\n","header {\r\n  position: relative;\r\n  top: 1em;\r\n  display: flex;\r\n  flex-wrap: nowrap;\r\n  flex-direction: column;\r\n  justify-content: space-around;\r\n  align-items: center;\r\n  font-size: 34px;\r\n  @media screen and (max-width: 500px){\r\n    font-size: 5vw;\r\n  }\r\n  font-weight: 400;\r\n  line-height: 77px;\r\n  letter-spacing: 0em;\r\n  text-align: center;\r\n  text-transform: uppercase;\r\n\r\n  &.SelectPage {\r\n    & h1 {\r\n      transition: all 0.3s;\r\n      font-size: 1em;\r\n      z-index: 1;\r\n    }\r\n    & .world {\r\n      transition: all 0.3s;\r\n      zoom: 0.5;\r\n    }\r\n  }\r\n}\r\n","@import url(\"https://fonts.googleapis.com/css2?family=Baloo+Thambi+2&display=swap\");\r\n\r\nbody {\r\n  font-family: \"Baloo Thambi 2\", sans-serif;\r\n  font-weight: 400;\r\n  font-size: $default-font-size;\r\n  line-height: 1.7;\r\n  color: $color-secondary;\r\n}\r\n.heading {\r\n  &-primary {\r\n    text-transform: uppercase;\r\n    text-shadow: 0 0 3em lighten($color-yellow, 15%);\r\n    line-height: 0;\r\n  }\r\n\r\n  &:hover {\r\n    transform: skewY(-2deg) skewX(-15deg) scale(1.1);\r\n    text-shadow: 0.5rem 1rem 2rem rgba($color-black, 0.2);\r\n  }\r\n}\r\n","svg {\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100vh;\r\n    z-index: -3;\r\n    @media print {\r\n        display: none;\r\n    }\r\n}","button {\r\n  width: fit-content;\r\n  height: fit-content;\r\n  background: #ffcd29;\r\n  border: 4px solid rgba(0, 0, 0, 0.2);\r\n  border-radius: 14px;\r\n  color: #5e1e43;\r\n  margin: 1em;\